@import url("https://use.typekit.net/frn6fkg.css");
h1, h2 {
    font-family: arboria, sans-serif;
    font-weight: 500;
    font-style: normal;
}

h3, h4, h5, h6 {
    font-family: arboria, sans-serif;
    font-weight: 400;
    font-style: normal;
}

p, span, a, li {
    font-family: dejanire-headline, serif;
    font-weight: 400;
    font-style: normal;
}

span.post-tag {
    font-family: arboria, sans-serif;
    font-weight: 500;
    font-style: normal;
}

nav ul li a {
    font-family: arboria, sans-serif;
    font-weight: 500;
    font-style: normal;
}

footer span {
    font-family: arboria, sans-serif;
    font-weight: 500;
    font-style: normal;
}

.posts-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}
@media (max-width: 1024px) {
    .posts-container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 768px) {
    .posts-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px) {
    .posts-container {
        grid-template-columns: repeat(1, 1fr);
    }
}

.ghc p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;

}

h3.ghc-updates {
    margin-top: 15px;
    margin-bottom: 30px;
}

p {
    font-size: 1.15em !important;
    line-height: 24px !important;
}

p.ghc-description {
    border: 1px solid rgba(0, 0, 0, 0.6);
    padding: 10px;
    width: 800px;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
